#waveform {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

#waveform-box {
    -ms-flex-positive: 1;
        flex-grow: 1;
    border:  1px solid #aaa;
    border-radius: 0 0.5em 0.5em 0.5em;
}

.visualizer-button {
    cursor: pointer;
    -webkit-box-shadow: none;
            box-shadow: none;
    background-color: transparent;
    border: none;
    color: #fff;
}

.visualizer-button.selected {
    border: 1px solid #aaa;
    border-bottom: none;

    border-radius: 0.5em 0.5em 0 0;
    z-index: 10;

    /* Sneaky way to erase the border underneath */
    -webkit-box-shadow: 0px 1px 0px #333;
            box-shadow: 0px 1px 0px #333;
}


