#root {
    height: 100%;
    padding: 0.5em;
}

#app {
    display: grid;
    height: 100%;

    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr;

    grid-template-areas:
        "name controls controls controls"
        "keyboard keyboard . lfo"
        ". . osc osc";

    -ms-flex-align: stretch;

        align-items: stretch;
    justify-items: stretch;
}

#keyboard-area {
    grid-area: keyboard;
    -ms-flex-direction: column;
        flex-direction: column;
}

#name-area {
    grid-area: name;
}

#controls-area {
    grid-area: controls;
}

#analyzer-area {
    grid-area: osc;
}

.container {
    display: -ms-flexbox;
    display: flex;
    padding: 0.5em;
}


