* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;

    font-family: 'IBM Plex Sans', sans-serif;

    background-color: #333;
    color: white;
    height: 100vh;
}

#version {
    -webkit-transform: translateY(-0.5em);
        -ms-transform: translateY(-0.5em);
            transform: translateY(-0.5em);
    font-size: 0.5em;
    color: #aaa;
}


